<template>
    <ion-page>
      <ion-header mode="ios">
        <ion-toolbar>
          <ion-title>Buat Jadwal Try Out Kedinasan</ion-title>
          <ion-back-button slot="start" default-href="/" />
        </ion-toolbar>
      </ion-header>
      <ion-content :fullscreen="true">
        <ion-content class="ion-padding">
          <ion-input
            v-model="judulUjian"
            type="text"
            placeholder="Masukkan judul ujian"
            @ionInput="handleJudulInput"
          ></ion-input>
  
          <ion-item>
            <ion-label>Tanggal Ujian</ion-label>
            <ion-button style="color: white" @click="showTanggalPicker = true">
              {{ tanggalUjian ? formatDate(tanggalUjian) : "Pilih Tanggal" }}
            </ion-button>
            <ion-datetime
              v-if="showTanggalPicker"
              v-model="tanggalUjian"
              display-format="DD MMM YYYY"
              placeholder="Pilih Tanggal"
              @ionChange="showTanggalPicker = false"
            ></ion-datetime>
          </ion-item>
          <ion-item>
            <ion-label>Durasi Ujian (Menit)</ion-label>
            <ion-input
              v-model.number="durasiUjian"
              type="number"
              placeholder="Masukkan durasi ujian dalam menit"
              @input="logDurasiUjian"
            ></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Pilih Soal</ion-label>
            <ion-select multiple v-model="selectedSoal" placeholder="Pilih Soal">
              <ion-select-option
                v-for="soal in daftarSoal"
                :key="soal.id"
                :value="soal.id"
              >
                {{ soal.question }}
              </ion-select-option>
            </ion-select>
          </ion-item>
          <ion-button style="color: white" expand="block" @click="buatJadwalUjian"
            >Buat Jadwal Ujian</ion-button
          >
          <ion-grid>
            <ion-row>
              <ion-col>
                <h2>Daftar Jadwal Ujian</h2>
                <ion-list>
                  <ion-item>
                    <ion-label>
                      <table class="table">
                        <thead>
                          <tr>
                            <th>Tanggal</th>
                            <th>Nama Ujian</th>
                            <th>Durasi (Menit)</th>
                            <th>Soal</th>
                            <th>Aksi</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="jadwal in daftarJadwal" :key="jadwal.id">
                            <td>{{ formatDate(jadwal.tanggalUjian) }}</td>
                            <td>{{ jadwal.judulUjian }}</td>
                            <td>{{ jadwal.durasi }}</td>
                            <td>{{ jadwal.soalCount }}</td>
                            <td>
                              <ion-button
                                style="color: white"
                                @click="hapusJadwal(jadwal.id)"
                                >Hapus</ion-button
                              >
                              <ion-button
                                :color="jadwal.aktif ? 'danger' : 'success'"
                                @click="toggleAktif(jadwal.id, jadwal.aktif)"
                              >
                                {{ jadwal.aktif ? "Nonaktifkan" : "Aktifkan" }}
                              </ion-button>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </ion-label>
                  </ion-item>
                </ion-list>
                <ion-button expand="block" @click="loadMore" v-if="showLoadMore"
                  >Load More</ion-button
                >
              </ion-col>
            </ion-row>
          </ion-grid>
        </ion-content>
        <ion-toast
          :is-open="toast.isOpen"
          :message="toast.message"
          :duration="toast.duration"
          :color="toast.color"
          @did-dismiss="() => (toast.isOpen = false)"
        ></ion-toast>
      </ion-content>
    </ion-page>
  </template>
  
  <script setup>
  import {
    IonInput,
    IonPage,
    IonHeader,
    IonToolbar,
    IonTitle,
    IonBackButton,
    IonContent,
    IonItem,
    IonLabel,
    IonDatetime,
    IonSelect,
    IonSelectOption,
    IonButton,
    IonGrid,
    IonRow,
    IonCol,
    IonList,
    IonToast,
  } from "@ionic/vue";
  import { ref, onMounted } from "vue";
  import {
    getFirestore,
    collection,
    getDocs,
    addDoc,
    doc,
    updateDoc,
    deleteDoc,
    query,
    orderBy,
  } from "firebase/firestore";
  import { firebaseApp } from "../firebase";
  
  const db = getFirestore(firebaseApp);
  
  const tanggalUjian = ref("");
  const durasiUjian = ref(0);
  const selectedSoal = ref([]);
  const daftarSoal = ref([]);
  const daftarJadwal = ref([]);
  const showTanggalPicker = ref(false);
  const judulUjian = ref("");
  
  const toast = ref({
    isOpen: false,
    message: "",
    duration: 2000,
    color: "success",
  });
  
  const fetchSoal = async () => {
    try {
      const querySnapshot = await getDocs(collection(db, "soalkedinasan"));
      daftarSoal.value = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
    } catch (error) {
      console.error("Error fetching soal:", error);
    }
  };
  
  const fetchJadwal = async () => {
    try {
      const querySnapshot = await getDocs(
        query(collection(db, "jadwalkedinasan"), orderBy("tanggalUjian"))
      );
      daftarJadwal.value = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
        soalCount: doc.data().soal.length,
      }));
    } catch (error) {
      console.error("Error fetching jadwal:", error);
      showToast("Terjadi kesalahan saat mengambil data jadwal.", "danger");
    }
  };
  
  const logDurasiUjian = (event) => {
    console.log("Durasi ujian input event:", event.target.value);
    durasiUjian.value = parseInt(event.target.value); // Convert to integer
  };
  
  const buatJadwalUjian = async () => {
    if (
      !judulUjian.value || // Validate judulUjian
      !tanggalUjian.value ||
      durasiUjian.value <= 0 ||
      selectedSoal.value.length === 0
    ) {
      showToast("Harap lengkapi semua field.", "danger");
      return;
    }
  
    try {
      await addDoc(collection(db, "jadwalkedinasan"), {
        judulUjian: judulUjian.value, // Add the title
        tanggalUjian: tanggalUjian.value,
        durasi: durasiUjian.value,
        soal: selectedSoal.value,
        aktif: true,
      });
      showToast("Jadwal ujian berhasil dibuat.", "success");
      resetForm();
      fetchJadwal();
    } catch (error) {
      console.error("Error creating jadwal:", error);
      showToast("Terjadi kesalahan saat membuat jadwal ujian.", "danger");
    }
  };
  
  const resetForm = () => {
    judulUjian.value = ""; // Reset judul ujian
    tanggalUjian.value = "";
    durasiUjian.value = 0;
    selectedSoal.value = [];
  };
  const handleJudulInput = (event) => {
    console.log("Judul Ujian input event:", event.target.value);
  };
  
  const hapusJadwal = async (jadwalId) => {
    try {
      await deleteDoc(doc(db, "jadwalkedinasan", jadwalId));
      showToast("Jadwal ujian berhasil dihapus.", "success");
      fetchJadwal();
    } catch (error) {
      console.error("Error deleting jadwal:", error);
      showToast("Terjadi kesalahan saat menghapus jadwal ujian.", "danger");
    }
  };
  
  const toggleAktif = async (jadwalId, isActive) => {
    try {
      await updateDoc(doc(db, "jadwalkedinasan", jadwalId), {
        aktif: !isActive,
      });
      showToast("Status jadwal ujian berhasil diubah.", "success");
      fetchJadwal();
    } catch (error) {
      console.error("Error updating jadwal status:", error);
      showToast("Terjadi kesalahan saat mengubah status jadwal.", "danger");
    }
  };
  
  const formatDate = (date) => {
    if (!date) return "N/A";
    const d = new Date(date);
    return d.toLocaleDateString("id-ID", {
      year: "numeric",
      month: "short",
      day: "numeric",
    });
  };
  
  onMounted(() => {
    fetchSoal();
    fetchJadwal();
  });
  
  const showToast = (message, color) => {
    toast.value = {
      isOpen: true,
      message,
      duration: 2000,
      color,
    };
  };
  </script>
  
  <style scoped>
  .table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  .table th,
  .table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  .table th {
    background-color: #f2f2f2;
  }
  
  .table tbody tr:hover {
    background-color: #f1f1f1;
  }
  </style>
  